import * as Yup from 'yup';
import { useEffect, useState, } from 'react';
import { useSnackbar } from 'notistack';
import { useNavigate } from 'react-router-dom';
// swal
import swal from 'sweetalert';
// form
import { useForm } from 'react-hook-form';
import { yupResolver } from '@hookform/resolvers/yup';
// @mui
import { LoadingButton } from '@mui/lab';
import { Box, Card, Grid, Stack, Alert, Typography } from '@mui/material';
import axios from '../../../../utils/axios';
import { FormProvider, RHFSelect, RHFTextField, RHFSwitch } from '../../../../components/hook-form';
// format number 
import { fCurrency, } from '../../../../utils/formatNumber';
import { planType } from '../../../../_mock';

import useAuth from '../../../../hooks/useAuth';

import Iconify from '../../../../components/Iconify';
import { PATH_DASHBOARD } from '../../../../routes/paths';

// ----------------------------------------------------------------------

export default function PurchaseData() {

  const NewUserSchema = Yup.object().shape({
    phone: Yup.string().required('Phone Number Required')
  });
  const pinSchema = Yup.object().shape({
    phone: Yup.string().required('Phone Number Required'),
    pin: Yup.string().min(4, 'Transaction Pin Must Be 4 Digits').max(4, 'Transaction Pin Must Be 4 Digit').required('Transaction Pin Required'),
  });
  const navigate = useNavigate();
  const { enqueueSnackbar } = useSnackbar();
  const [Habukhannetwork, setNetwork] = useState();
  const [NumberValidator, SetValidate] = useState('');
  const [verifynetwork, SetVerifyNetwork] = useState();
  const [typevalue, settypevalue] = useState();
  const [dataplan, setplans] = useState([]);
  const [network, realNetwork] = useState();
  const [purchasecode, Getpurchasecode] = useState();
  const [Habukhanconfirm, GetHabukhanconfirm] = useState(null);
  const AccessToken = window.localStorage.getItem('accessToken');
  const { setting } = useAuth();
  const pin = setting?.setting?.allow_pin === 1;
  const methods = useForm({
    resolver: yupResolver(pin ? pinSchema : NewUserSchema),
  });
  const [amount, SetAmount] = useState();
  const {
    setValue,
    handleSubmit,
    formState: { errors, isSubmitting },
  } = methods;

  // get network over here
  useEffect(() => {
    axios.get('/api/website/app/network').then(data => {
      setNetwork(data.data?.network);
    });
  }, []);

  const changeme = async (me) => {
    if (me) {
      settypevalue('');
      realNetwork(me)
      Getpurchasecode('')
      setplans([])
      SetAmount(undefined)
      await axios.get(`/api/verify/network/${me}/habukhan/system`).then(data => {
        SetVerifyNetwork(data.data.network);
      }).catch(SetVerifyNetwork(undefined));
    } else {
      SetVerifyNetwork(undefined);
    }
  }
  const onSubmit = async (data) => {
    if (network === '1') {
      theNetwork = 'MTN';
    } else if (network === '2') {
      theNetwork = 'AIRTEL';
    } else if (network === '3') {
      theNetwork = 'GLO';
    } else if (network === '4') {
      theNetwork = '9MOBILE';
    } else {
      theNetwork = network;
    }
    try {
      const isConfirmed = await swal({
        title: 'Confirmation',
        text: `You are on the verge of gifting ${Habukhanconfirm} of data to the ${theNetwork} number ${data?.phone}.`,
        icon: 'warning',
        buttons: ['Cancel', 'Confirm'],
      });
      if (isConfirmed) {
        const resp = await axios.post('/api/data', {
          network,
          phone: data.phone,
          data_plan: purchasecode,
          bypass: data?.bypass || false,
          pin: data.pin,
          token: AccessToken,
        });

        if (resp?.data?.status === 'success') {
          const invoiceOption = await swal({
            title: 'Success',
            text: `${resp?.data?.message}`,
            icon: 'success',
            buttons: ['Okay', 'Invoice'],
          });
          if (invoiceOption) {
            navigate(`${PATH_DASHBOARD.general.invioce}/${resp?.data?.transid}/data`, { replace: true });
          }
          window.location.reload();
        } else if (resp?.data?.status === 'fail') {
          swal('Oops', `${resp?.data?.message}`, 'error');
        } else {
          swal('Info', `${resp?.data?.message}`, 'info');
        }
      }
    } catch (error) {
      if (error?.data?.message !== undefined) {
        swal('Oops', `${error?.data?.message}`, 'error');
      } else if (error?.message !== undefined) {
        swal('Oops', `${error?.message}`, 'error');
      } else {
        swal('Oops', 'Something went wrong', 'error');
      }
    }
  };

  let theNetwork;
  let verNetwork;
  let ic;
  const checkphone = (phone) => {
    const phoneNumber = phone.target.value;
    setValue('phone', phoneNumber)
    if (network !== null && network !== undefined && network !== '') {

      if (network === '1') {
        theNetwork = 'MTN';
      } else if (network === '2') {
        theNetwork = 'AIRTEL';
      } else if (network === '3') {
        theNetwork = 'GLO';
      } else if (network === '4') {
        theNetwork = '9MOBILE';
      } else {
        theNetwork = network;
      }
      if (phoneNumber === "" || phoneNumber.length < 6) {
        SetValidate('')
      } else {
        SetValidate('')
        if (/0702|0704|0803|0806|0703|0706|0813|0816|0810|0814|0903|0906|0913/.test(phoneNumber)) {
          verNetwork = "MTN";
        } else if (/0805|0807|0705|0815|0811|0905/.test(phoneNumber)) {
          verNetwork = "GLO";
        } else if (/0702|0704|0803|0806|0703|0706|0813|0816|0810|0814|0903|0906|0913/.test(phoneNumber)) {
          verNetwork = "GIFTING";
        }
        else if (/0802|0808|0708|0812|0701|0901|0902|0907|0912/.test(phoneNumber)) {
          verNetwork = "AIRTEL";
        }
        else if (/0809|0818|0817|0908|0909/.test(phoneNumber)) {
          verNetwork = "9MOBILE";
        }
        else if (/0804/.test(phoneNumber)) {
          verNetwork = "NTEL";
        }
        else {
          verNetwork = "Unable to identify network !";
        }
        if (verNetwork === theNetwork) {
          ic = <Iconify icon="icon-park:success" sx={{ width: 20, height: 20, color: 'success.main' }} />
        }
        else {
          ic = <Iconify icon="akar-icons:circle-alert" sx={{ width: 20, height: 20, color: 'error.main' }} />
        }

        SetValidate(<>
          <Typography variant="subtitle2" sx={{ mb: 0.5 }}> Network is: <b>{verNetwork} </b>{ic}<br /><i><b> NB: </b> Ignore this warning for <b>Ported Numbers</b></i></ Typography> </>);
      }


    } else {
      enqueueSnackbar('Network is Required', { variant: 'error' })
    }
  }
  const dataPlan = async (me) => {
    settypevalue(me);
    Getpurchasecode('')
    setplans([]);
    SetAmount(undefined)
    await axios.post(`/api/get/data/plans/${AccessToken}/habukhan`, {
      network,
      network_type: me
    }).then(data => setplans(data.data.plan));
  }
  const GetDataCode = async (me) => {
    Getpurchasecode(me.target.value)
    const value = dataplan.filter((item) => item.plan_id === me.target.value)
    if (value[0]) {
      SetAmount(`₦${fCurrency(value[0].amount)}`);
      GetHabukhanconfirm(value[0].name);
    }
  }
  return (
    <FormProvider methods={methods} onSubmit={handleSubmit(onSubmit)}>
      <Grid container spacing={3}>
        <Grid item xs={12} md={8}>
          {!!errors.afterSubmit && <Alert severity="error">{errors.afterSubmit.message}</Alert>}
          <Card sx={{ p: 3 }}>
            <Box
              sx={{
                display: 'grid',
                columnGap: 1,
                rowGap: 3,
                gridTemplateColumns: { xs: 'repeat(1, 1fr)', sm: 'repeat(1, 1fr)' },
              }}
            >
              <RHFSelect name="network" label="Network" placeholder="Network" onChange={(e) => changeme(e.target.value)} value={network}>
                <option value="" />
                {Habukhannetwork?.map((option) => (
                  <option key={option?.plan_id} value={option?.plan_id} disabled={option?.network_sme === 0 && option?.network_cg === 0 && option?.network_g === 0}>
                    {option?.network}
                  </option>
                ))}
              </RHFSelect>

              <RHFSelect name="type" label="Plan Type" placeholder="Plan Type" value={typevalue} onChange={(e) => dataPlan(e.target.value)}>
                <option value="" />
                {verifynetwork !== undefined && planType?.map((option) => (
                  <option key={option?.label} value={option?.label} disabled={option?.code === 1 && verifynetwork?.network_g === 0 || option?.code === 2 && verifynetwork?.network_sme === 0 || option?.code === 3 && verifynetwork?.network_cg === 0}>
                    {option?.label}
                  </option>
                ))}
              </RHFSelect>
              <RHFSelect name="plan" label="Data Plan" placeholder="Data Plan" onChange={GetDataCode} value={purchasecode}>
                <option value="" />
                {dataplan?.map((option) => (
                  <option key={option?.plan_id} value={option?.plan_id}>
                    {option?.name}
                  </option>
                ))}
              </RHFSelect>

              <RHFTextField name="phone" label="Phone Number" type="number" inputProps={{ inputMode: 'numeric', pattern: '[0-9]*' }} onChange={checkphone} />
              <>{NumberValidator}</>
              {pin && <RHFTextField name="pin" label="Transaction Pin" type="password" autoComplete="new-password" pattern="[0-9]*" inputmode="numeric" />}

              {amount !== undefined && <RHFTextField name="amount" label="Amount" value={amount} disabled />}
              <RHFSwitch
                name='bypass'
                labelPlacement="start"
                def={false}
                label={
                  <>
                    <Typography variant="subtitle2" sx={{ mb: 0.5 }}>
                      Bypass Phone Number
                    </Typography>
                  </>
                }
                sx={{ mx: 0, width: 1, justifyContent: 'space-between' }}
              />
            </Box>
            <Stack alignItems="flex-end" sx={{ mt: 3 }}>
              <LoadingButton fullWidth size="large" type="submit" variant="contained" loading={isSubmitting}>
                Purchase
              </LoadingButton>
            </Stack>
          </Card>
        </Grid>
      </Grid>
    </FormProvider>
  );
}
